.cart{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.cartitem{
    width: 700px;
   height: 250px;
   display: flex;
  align-items: center;
 box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
 border-radius: 25px;
 margin: 30px;
}
.cartitem img{
    width: 200px;
}
.cartitem .description{
    width: 100%;
    font-size: 30px;
}
.counthandler input{
    width: 40px;
    text-align: center;
    font-weight: bolder;
}
.checkout button{
    width: 150px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 8px;
   margin: 10px;
   cursor: pointer;
}